.form-row .give-stripe-cc-field {
  box-sizing: border-box;
  width: 100%;
  border-radius: 0;
  outline: none;
  color: #333;
  background-color: #fdfdfd;
  border: 1px solid #ccc;
  margin: 0;
  padding: .5em;
}

.give-stripe-cc-field.give-fl-input {
  height: auto;
  min-height: 50px;
  line-height: inherit;
  padding: 14px 10px;
  background-color: #FFF;
}

.give-stripe-cc-field-wrap .give-fl-is-active .give-fl-input {
  padding: 20px 12px 4px;
  transition: padding 0.2s ease-in-out, border 0.2s ease-in-out;
}

.give-stripe-cc-field-wrap .give-fl-label span {
  display: none;
}

.give-stripe-single-cc-field-wrap {
  border: 1px solid #CCCCCC;
  background-color: #FDFDFD;
  padding: 10px;
  height: 40px;
  margin: 0 0 15px 0;
}

.give-form .give-stripe-cc-field.focus,
.give-form .give-stripe-cc-field:focus {
  background-color: #FFFFFF;
}

ul.give-stripe-payment-tabs {
  display: block;
  margin: 15px 0;
  border-bottom: 1px solid #CCCCCC;
}

ul.give-stripe-payment-tabs li {
  float: left;
  list-style: none;
  margin-right: 10px;
  border: 1px solid #CCCCCC;
  border-bottom: 1px solid #ffffff;
  -webkit-border-radius: 3px 3px 0 0;
  -moz-border-radius: 3px 3px 0 0;
  border-radius: 3px 3px 0 0;
}

ul.give-stripe-payment-tabs li.active {
  border-bottom: 2px solid #FFFFFF;
  margin-bottom: -1px;
}

ul.give-stripe-payment-tabs li a {
  display: block;
  overflow: hidden;
  font-size: 18px;
  padding: 9px 10px;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
  outline: none;
}

ul.give-stripe-payment-tabs li:first-child a span {
  float: left;
}

ul.give-stripe-payment-tabs li span.give-stripe-card-icon {

}

ul.give-stripe-payment-tabs li span.give-stripe-card-icon img {
  width: 30px;
}

ul.give-stripe-payment-tabs li span.give-stripe-card-text {
  margin-left: 5px;
  font-size: 16px;
}

ul.give-stripe-payment-tabs li span.give-stripe-payment-request-icon img {
  width: 55px;
  vertical-align: middle;
}


ul.give-stripe-payment-tabs li a:focus {
  background-color: #E5E5E5;
}

ul.give-stripe-payment-tabs li a:focus,
ul.give-stripe-payment-tabs li a:hover {
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
}

ul.give-stripe-payment-tabs li.active a {
  display: block;
  background-color: transparent;
}

ul.give-stripe-payment-tabs li.active a:focus span:last-child {
  border-bottom: 1px solid #222222;
}

.give-stripe-payment-request-button-wrap {
  margin: 0 0 15px 0;
}

.give-stripe-credit-card-content {
  .give-stripe-cc-fields-container {
    overflow: hidden;
  }
}


